import { merge } from '../common/util'
import edgeSchema from './edge-schema.json'
import nodeSchema from './node-schema.json'
/****
 * node(cytoscape)
 */
const nodesBaseStyle = {
  'z-index': 2
}
/****
 * edge(cytoscape)
 */
const edgesBaseStyle = {
  'z-index': 1
}
const baseColor = ['#c23531', '#2f4554', '#61a0a8', '#d48265', '#91c7ae', '#749f83', '#ca8622', '#bda29a', '#6e7074', '#546570', '#c4ccd3']
/**
 * 
 * 
 * {
 *   key: 
 *   styles: Array/Object
 * }
 * 
 * [{
 *    name: '1',
 *    matching: data => data.label === '1', // 
 *    style, // cytoscape
 *    formatter: name => name // 
 * }]
 * **/
const categoryOption = {
  nodes: {
    key: 'category',
    styles: baseColor.map(color => merge({}, nodesBaseStyle, {
      'background-color': color,
      'border-color': color
    }))
  },
  edges: {
    key: 'category',
    styles: [JSON.parse(JSON.stringify(edgesBaseStyle))]
  }
}
/**
 * cytoscape cytoscapecytoscape: http://js.cytoscape.org/#introduction
 * **/
const cytoscapeOption = {
  style: [
    {
      selector: ':active',
      style: {
        'overlay-opacity': 0
      }
    },
    {
      selector: 'node',
      style: JSON.parse(JSON.stringify(nodesBaseStyle))
    },
    {
      selector: 'edge',
      style: JSON.parse(JSON.stringify(edgesBaseStyle))
    },
    {
      selector: '.hover',
      style: {
        'background-opacity': 1,
        'background-image-opacity': 1,
        'z-index': 99
      }
    },
    {
      selector: 'edge.hover',
      style: {
        width: 2,
        'z-index': 98
      }
    },
    {
      selector: '.unhover',
      style: {
        'opacity': 0.3
        // 'z-index': 0
      }
    },
    {
      selector: 'edge.unhover',
      style: {
        width: 1,
        'opacity': 0.3
      }
    }
  ]
  // minZoom: 0.5,
  // maxZoom: 10
}
export default {
  categoryOption,
  cytoscapeOption,
  edgeSchema,
  nodeSchema
}
export {
  categoryOption,
  cytoscapeOption,
  edgeSchema,
  nodeSchema
}
